.card-front {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 3px 3px 10px black;
    border-radius: 10px;
    /* position: relative; */
    opacity: 1.0;
    transition: all .3s ease;
}

.card-back {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 3px 3px 10px black;
    border-radius: 10px;
    /* position: relative; */
    opacity: 1.0;
    transition: all .3s ease;
}

.card-back-image {
    width: 100%;
    height: 100%;
    -webkit-user-drag: none;
    user-select: none;
}
