.timeCounter-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.timeCounter-time {
    display: flex;
    flex-direction: row;
    justify-content: space-between ;

}
.timeCounter-restartButton {
    background-color:cornflowerblue;
    color: white;
    border: none;
    padding: .5rem;
    border-radius: .3rem;
    margin-top: .5rem;
    opacity: 1;
}

.timeCounter-restartButton:hover {
    cursor: pointer;
    opacity: .7;
}

.timeCounter-time > .dynamic2 {
    min-width: 6rem;
    text-align: end;
}